* {
  font-family: -apple-system, BlinkMacSystemFont, Segoe UI, Roboto, Oxygen, Ubuntu, Cantarell, Open Sans, Helvetica Neue, sans-serif;
}

body {
  background-color: #d7f3c8;
  height: 100vh;
}

.title {
  text-align: center;
}

.sign-in-form {
  place-content: center;
  display: grid;
}

.sign-in-helper-text {
  width: 75%;
  margin: 0 auto;
}

.form-input {
  margin-top: 1rem;
  display: grid;
}

.form-input .input {
  width: 75%;
  margin-inline: auto;
  padding: 1rem;
}

.form-input .btn {
  color: #fff;
  margin-top: 1rem;
  background-color: teal;
  width: 75%;
  margin-inline: auto;
  padding: 1rem;
}

.item-list {
  margin-top: 5rem;
  justify-content: space-around;
  gap: 1rem;
  max-width: 90%;
  margin-inline: auto;
  padding-bottom: 5rem;
  display: flex;
}

.item-list > .item {
  border: 2px solid #000;
  border-radius: 20px;
  flex: 1;
  gap: 2rem;
  padding: 2rem;
}

@media (width <= 500px) {
  .item-list {
    flex-direction: column;
  }
}

.item .item-name {
  color: #000;
  font-size: 2rem;
  font-weight: bold;
}

.item .item-footer {
  display: grid;
}

.item .item-footer .item-price {
  font-size: 1.5rem;
}

.btn {
  cursor: pointer;
  border: none;
  border-radius: 20px;
  padding: 1rem .5rem;
}

.purchase-btn {
  color: #fff;
  background-color: #1ba100;
  margin-top: 1rem;
}

.purchase-btn:hover {
  background-color: #147500;
}

.download-btn {
  color: #fff;
  background-color: #8d00c5;
  margin-top: 1rem;
}

.download-btn:hover {
  background-color: #6500a0;
}

